import * as React from "react";
import type { HeadFC, PageProps } from "gatsby";
import {
  SectionHeader,
  Header,
  Main,
  Stack,
  SubHeader,
  Paragraph,
  Link,
  Section,
  RowStack,
  ExpSection,
  LinkStack,
} from "../components/index.styled";
import "../styles/index.css";
import { experiences, links, skills } from "../constants";
import Skill from "../components/skills";
import NavigationHeader from "../components/navigation-header";
import exp from "constants";
import Experience from "../components/exp";

const IndexPage: React.FC<PageProps> = () => {
  return (
    <Main>
      <NavigationHeader />
      <Section>
        <Header>Hi!</Header>
        <Header>I'm Mary Jane Aguelo</Header>
        <SubHeader>Software Engineer</SubHeader>
        <Paragraph>
          Hello, I'm MJ. I have developed different types of websites and mobile
          applications. Passionate with building complex applications. Always
          open to new learning and technologies.
        </Paragraph>
      </Section>
      <Section>
        <Stack>
          {skills.map((skill) => {
            return <Skill skills={skill} />;
          })}
        </Stack>
      </Section>
      <ExpSection>
        <RowStack>
          {experiences.map((i) => {
            return <Experience exp={i} />;
          })}
        </RowStack>
        
      </ExpSection>
      <LinkStack>
          {links.map((i) => {
            return (
              <Link href={i.link} target="_blank">
                {i.name}
              </Link>
            );
          })}
        </LinkStack>
    </Main>
  );
};

export default IndexPage;

export const Head: HeadFC = () => <title>MJA</title>;
